<template>
  <div>
    <div class="page startPage">
      <div id="logo">跳一跳1.0.2</div>
      <div class="btn start-btn" @click="startGame">GAME START <i class="fa fa-play-circle"></i></div>
    </div>
    <div class="page restartPage" style="display: none;">
      <div id="final-score">
        <div class="hint">== 本局分数 ==</div>
        <div class="score"></div>
        <div class="wrapper" style="position: absolute; top: 0; text-align: center; width: 100%;">
          <img src="../assets/img/particle.png" alt="">
        </div>
      </div>
      <div class="btn restart-btn" @click="restartGame">PLAY AGAIN <i class="fa fa-play-circle"></i></div>
    </div>
    <div class="MyToast disappear"></div>
    <nav-menu :showValue="isShowNavMenu" @childClick="updateParentValue"></nav-menu>
    <div class="nav blindBox">
      <ul>
        <li class="goblindbox">开盲盒</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
import { init } from '../game/index.js'
import Game from '../game/game.js';
import NavMenu from '@/components/NavMenu.vue'

export default {
  name: 'Home',
  components: {
    NavMenu
  },
  setup() {
    const isShowNavMenu = ref(false);

    const showToast = (value) => {
      isShowNavMenu.value = value;
    };

    return {
      isShowNavMenu,
      showToast,
    }
  },
  mounted() {
    console.log(process.env.ROUTE_URL)
    const isFirstVisit = window.localStorage.getItem('hasVisitedtwo') || '';
    if (!isFirstVisit) {
      this.initializeGame();
    }
    this.initializeGame();

    // 调用游戏初始化函数init();
    /*const game = new Game();
    window.$game = game;
*/
    init()
    console.log('******game',window,window.$game)

    this.bindEvents();
  },
  methods: {
    startGame() {
      this.showToast(true);
      const startpage = document.querySelector('.startPage');
      const navMenu = document.querySelector('.navMenu');

      startpage.style.display = 'none';
      window.$game.start();
    },
    restartGame() {
      this.showToast(true);
      const restartpage = document.querySelector('.restartPage');
      const navMenu = document.querySelector('.navMenu');

      restartpage.style.display = 'none';

      window.$game.restart();
    },
    login() {
      console.log('login');
    },
    bindEvents() {
      const game = window.$game;
      const restartpage = document.querySelector('.restartPage');
      const scoreEl = document.querySelector('.score');
      const blindBox = document.querySelector('.blindBox');
      const navMenu = document.querySelector('.navMenu');

      game.failCallback = (score) => {
        this.showToast(false);
        restartpage.style.display = 'flex';
        blindBox.style.display = 'none';
        scoreEl.innerHTML = score;
      };

    },
    initializeGame() {
      const routeUrl = process.env.ROUTE_URL || '../';

      // 初始化游戏逻辑
      const files = [
        "cd.mtl", "cd.obj", "cheng.mtl", "cheng.obj", "d_hezi4.mtl", "d_hezi4.obj",
        "d_hezi5.mtl", "d_hezi5.obj", "d_pixiang.mtl", "d_pixiang.obj", "d_tiaotai.mtl",
        "d_tiaotai.obj", "d_xiaoniao.mtl", "d_xiaoniao.obj", "d_yuanzhu1.mtl", "d_yuanzhu1.obj",
        "dangao.mtl", "dangao.obj", "huaban.mtl", "huaban.obj", "pingtai1.mtl", "pingtai1.obj",
        "q_hezi4.mtl", "q_hezi4.obj", "q_shudun.obj", "q_shudun.mtl", "shoutixiang.obj",
        "shoutixiang.mtl", "shudun.mtl", "shudun.obj", "x_fangzhuo.mtl", "x_fangzhuo.obj",
        "x_hezi5.mtl", "x_hezi5.obj", "x_huaban.mtl", "x_huaban.obj", "x_xigua.mtl",
        "x_xigua.obj", "x_yuanzhu1.mtl", "x_yuanzhu1.obj",
      ];
      files.forEach(file => {
        fetch(routeUrl + 'assets/obj/' + file)
          .then(response => {
            if (response.ok) {
              return response.text();
            } else {
              throw new Error(`Failed to load file: ${file}`);
            }
          })
          .then(data => {
            // 处理加载的文件数据
            // 在这里可以执行适当的操作，如渲染3D模型等
          })
          .catch(error => {
            console.error(error);
          });
      });

      window.localStorage.setItem('hasVisitedtwo', 'true')
      // Telegram WebApp 初始化
      if (window.Telegram) {
        console.log('T', Telegram);
        Telegram.WebApp.ready();
        const urlParams = new URLSearchParams(window.location.search);
        const userId = urlParams.get('user_id');
        console.log('userId', userId);
      }
    },
    updateParentValue(value) {
      this.isShowNavMenu = value;
      this.showToast(value);
      console.log('updateParentValue', value);
    }
  }
}
</script>

